body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

button {
  background-color: transparent;
  border: none;
  cursor: pointer;
  outline: none;
  padding: 0;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
}

.search-area form {
  display: -webkit-flex;
  display: flex;
  height: 38px;
  width: 50%;
  padding: 2px 15px;
  margin: 0 auto;
  border: solid 1px #dfe1e5;
  border-radius: 25px;
}

.search-area input {
  display: block;
  -webkit-flex: 1 1 auto;
          flex: 1 1 auto;
  box-sizing: border-box;
  height: 100%;
  font-size: 1.2em;
  border: none;
  outline: 0;
  padding: 5px 4px 0 20px;
}

.search-area input::-webkit-input-placeholder {
  color: #dfe1e5;
}

.search-area input::-ms-input-placeholder {
  color: #dfe1e5;
}

.search-area input::placeholder {
  color: #dfe1e5;
}

.search-area form:hover {
  box-shadow: 0 0 10px #aaa;
}

.search-area button {
  height: 70%;
  width: 27px;
  margin: auto 0;
  -webkit-mask-image: url(/reactApp/static/media/search-solid.c6a0effb.svg);
          mask-image: url(/reactApp/static/media/search-solid.c6a0effb.svg);
  background-color: #4185f4;
  background-size: cover;
}

html {
  margin: 20px
}

.logo img {
  height: 92px;
  width: 272px;
}

.logo {
  margin: 40px auto 5px auto;
  text-align: center;
}

.container {
  border: solid 1px #dfe1e5;
  border-radius: 15px;
  color: #878787;
  display: -webkit-flex;
  display: flex;
  margin: 10px 20%;
}

.main {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  padding: 5% 5% 5% 10%;
  font-size: 25px;
}

.place {
  font-size: 45px;
  line-height: 180%;
}

.desc {
  font-size: 20px;
}

.icon-container {
  display: -webkit-flex;
  display: flex;
  padding: 50px;
}

.icon {
  width: 150px;
  height: 150px;
  margin-right: 20px;
}

.temp span {
  color: #000;
  font-size: 70px;
  line-height: 150px;
}

